<template>
    <div>
        <div class="col-12 d-flex justify-content-between">
            <p class="card-subtitle mb-3">
                A continuación carge la lista de participantes:
            </p>
            <a :href="format" download target="_blank" class="card-subtitle mb-3"><b>Descargar Formato</b></a>
        </div>
        <div class="col-12">
            <div class="form-group">
                <label>Archivo : <span class="text-muted">(Opcional)</span></label>
                <v-app>
                    <v-file-input v-model="data.file"
                      :loading="upload > 0"
                      @click:clear="onClear"
                      @change="onValidate"
                      class="form-control d-flex"
                      placeholder="Seleccionar archivo"
                      outlined
                      hide-details
                      accept=".xlsx, .xls, .csv"
                      dense>
                    </v-file-input>
                </v-app>
                <span>Se cargaron {{ data.participants ? data.participants : 0 }} participantes</span>
            </div>
        </div>
    </div>
</template>
<script>
import format from '@/assets/Formato.xlsx'
export default {
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      format,
      upload: 0
    }
  },
  methods: {
    onValidate: function (event) {
      if (this.data.file) {
        this.upload++
        this.$emit('onUpload', true)
        if (this.upload === 1) {
          let form = new FormData()
          form.append('file', this.data.file)
          this.$axios({
            url: '/validate/participants',
            method: 'POST',
            data: form
          })
            .then((resp) => {
              this.data.participants = resp.data
              this.$emit('onUpload', false)
              this.upload = 0
            })
            .catch((err) => {
              let message = err.response ? err.response.data.message : err.message
              this.$swal({ toast: true, icon: 'error', title: message, position: 'top-end', showConfirmButton: false, timer: 3000 })
              this.$emit('onUpload', false)
              this.upload = 0
            })
        }
      } else {
        this.$emit('onUpload', false)
        this.upload = 0
      }
    },
    onClear: function (e) {
      this.data.participants = 0
      this.data.file = null
      this.$emit('onUpload', false)
      this.upload = 0
    }
  }
}
</script>



